.about__container {
    display: grid;
    grid-template-columns: 35% auto;
    gap: 3%;
}

.about__me {
    width: 80%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    /* background: linear-gradi, var(--color-secondary-1),ent(45deg, transparent, transparent); */
    display: grid;
    place-items: center;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
    position: relative;
    left: 1rem;
    top: 2rem;
}

.about__me:hover {
    width: 20rem;
    transform: rotate(0deg);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 1rem 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: transparent;
    cursor: default;
    border-color: var(--color-light);
}

.about__icon {
    color: var(--color-main);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light)
}

.about__content p {
    margin: 2rem 0 2.6rem 0;
    color: var(--color-light);
}

.about__content {
    width: 100%;
}

/* ========== Media Query (Medium Screens) ========== */
@media screen and (max-width: 1024px){
    .about__container {
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me {
        transform: rotate(0deg);
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p {
        margin: 1rem 0 1.5rem;
    }
}

/* ========== Media Query (Small Screens) ========== */
@media screen and (max-width: 600px){
    .about__me {
        transform: rotate(0deg);
        width: 65%;
        margin: 0 auto 4rem;
        top: 0rem;
        left: 0rem;
    }

    .about__cards {
        width: 90vw;
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }

    .about__content p {
        margin: 1.5rem 0;
    }
}

#about {
    text-align: center;
    height: auto;
    width: auto;
    overflow: hidden;
    object-fit: cover;
}