@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
header {
    height: auto;
    padding-top: 7rem;
    overflow: hidden;
    object-fit: cover;
}

.header__container {
    text-align: center;
    height: auto;
    position: relative;
    overflow: hidden;
    object-fit: cover;
}

/* ========== CTA ========== */
.cta {
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

/* ========== Socials ========== */

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-main);
}

/* ========== Scroll ========== */

.scroll__down {
    position: absolute;
    right: -2.2rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 400;
    font-size: 0.9rem ;
}


/* ========== Media Query (Medium Screens) ========== */
@media screen and (max-width: 1024px){
    header {
        height: 100vh;
    }
    .header__socials {
        display: flex;
        flex-direction: column;
        align-items: center;
        grid-gap: 0.8rem;
        gap: 0.8rem;
        position: absolute;
        left: 0;
        bottom: 6rem;
    }
    .scroll__down {
        position: absolute;
        right: -2.2rem;
        bottom: 10rem;
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
        font-weight: 400;
        font-size: 0.9rem ;
    }
}

/* ========== Media Query (Small Screens) ========== */
@media screen and (max-width: 600px){
    header {
        height: 100vh;
    }
    .header__socials, .scroll__down {
        display: none;
    }
}

/* ========== Logo ========== */
.logo {
    background: linear-gradient(var(--color-secondary-2), transparent);
    width: 22rem;
    height: 22rem;
    position: relative;
    left: calc(50% - 11rem); /* 11rem is the 0.5*width */
    margin-top: 1rem;
    border-radius: 10rem 10rem 0 0;
}

.logo > img {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    position: relative;
    top: -2rem;
}
.about__container {
    display: grid;
    grid-template-columns: 35% auto;
    grid-gap: 3%;
    gap: 3%;
}

.about__me {
    width: 80%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    /* background: linear-gradi, var(--color-secondary-1),ent(45deg, transparent, transparent); */
    display: grid;
    place-items: center;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: var(--transition);
    position: relative;
    left: 1rem;
    top: 2rem;
}

.about__me:hover {
    width: 20rem;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 1rem 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: transparent;
    cursor: default;
    border-color: var(--color-light);
}

.about__icon {
    color: var(--color-main);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light)
}

.about__content p {
    margin: 2rem 0 2.6rem 0;
    color: var(--color-light);
}

.about__content {
    width: 100%;
}

/* ========== Media Query (Medium Screens) ========== */
@media screen and (max-width: 1024px){
    .about__container {
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }

    .about__me {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p {
        margin: 1rem 0 1.5rem;
    }
}

/* ========== Media Query (Small Screens) ========== */
@media screen and (max-width: 600px){
    .about__me {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        width: 65%;
        margin: 0 auto 4rem;
        top: 0rem;
        left: 0rem;
    }

    .about__cards {
        width: 90vw;
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }

    .about__content p {
        margin: 1.5rem 0;
    }
}

