/* ========== Animations ========== */

@-webkit-keyframes pan_image {
    0% {
        background-position: 36% 42%;
        background-size: 150%;
    } 20% {
        background-position: 30% 35%;
        background-size: 150%;
    } 20.0001% {
        background-position: 60% 85%;
        background-size: 150%;
    } 40% {
        background-position: 49% 81%;
        background-size: 150%;
    } 40.0001% {
        background-position: 80% 42%;
        background-size: 150%;
    } 60% {
        background-position: 84% 33%;
        background-size: 150%;
    } 60.0001% {
        background-position: 0% 0%;
        background-size: 150%;
    } 80% {
        background-position: 15% 4%;
        background-size: 150%;
    } 80.0001% {
        background-position: 80% 10%;
        background-size: 150%;
    } 100% {
        background-position: 72% 14%;
        background-size: 150%;
    }
}

@keyframes pan_image {
    0% {
        background-position: 36% 42%;
        background-size: 150%;
    } 20% {
        background-position: 30% 35%;
        background-size: 150%;
    } 20.0001% {
        background-position: 60% 85%;
        background-size: 150%;
    } 40% {
        background-position: 49% 81%;
        background-size: 150%;
    } 40.0001% {
        background-position: 80% 42%;
        background-size: 150%;
    } 60% {
        background-position: 84% 33%;
        background-size: 150%;
    } 60.0001% {
        background-position: 0% 0%;
        background-size: 150%;
    } 80% {
        background-position: 15% 4%;
        background-size: 150%;
    } 80.0001% {
        background-position: 80% 10%;
        background-size: 150%;
    } 100% {
        background-position: 72% 14%;
        background-size: 150%;
    }
}

