/* @import url('https://fonts.googleapis.com/css2?family=Open+sans:wght@500;400;300;600&display=swap'); */

* {
    padding: 0%;
    margin: 0%;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style-type: none;
    text-decoration: none;
    overflow-x: hidden;
}

:root {
    --color-bg: #0D0E17;
    --color-bg-variant: rgb(22, 26, 44);
    --color-secondary-1: #7AFE00;
    --color-secondary-2: rgb(183, 0, 240);
    --color-light: #A7ABCE;
    --transition: all 400ms ease;
    --color-main: white;
    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

#blur {
    height: 100%;
    width: 100%;
    top: 0;
    left : 0;
    position: fixed;
    z-index: 2;
    -webkit-backdrop-filter: blur(200px);
            backdrop-filter: blur(200px);
    scroll-behavior: smooth;
}

/* html {
    
} */

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Poppins', monospace;
    background: #0D0E17;
    background: var(--color-bg);
    color: white;
    color: var(--color-main);
    line-height: 1.7;
}

/* ========== General Styles ========== */
.container {
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
    object-fit: cover;
    height: 100%;
}

h1, 
h2, 
h3, 
h4, 
h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
    height: 100vh;
}

section > h2, section > h5 {
    text-align: center;
    color: #A7ABCE;
    color: var(--color-light)
}

section > h2 {
    color: white;
    color: var(--color-main);
    margin-bottom: 3rem;
}

.text-light {
    color: #A7ABCE;
    color: var(--color-light);
}

a {
    color: white;
    color: var(--color-main);
    transition: all 400ms ease;
    transition: var(--transition);
}

a:hover {
    color: rgb(183, 0, 240);
    color: var(--color-secondary-2);
}

.btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: white;
    color: var(--color-main);
    text-align: center;
    padding: 1rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid white;
    border: 1px solid var(--color-main);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn:hover {
    background: rgb(183, 0, 240);
    background: var(--color-secondary-2);
    color: #0D0E17;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: white;
    background: var(--color-main);
    color: #0D0E17;
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

#blob {
    background-color: #0D0E17;
    background-color: var(--color-bg);
    height: 300px;
    aspect-ratio: 1;
    position: absolute;
    left: 50%;
    top: 50%;
    translate: -50% -50%;
    border-radius: 50%;
    background: rgb(92, 92, 255);
    -webkit-animation: rotate 20s infinite;
            animation: rotate 20s infinite;
    opacity: 0;
    transition: opacity 300ms ease;
}

body:hover > #blob {
    opacity: 1;
}
  
@-webkit-keyframes rotate {
    from {
        rotate: 0deg;
    } to {
        rotate: 360deg;
    }
}
  
@keyframes rotate {
    from {
        rotate: 0deg;
    } to {
        rotate: 360deg;
    }
}

/* ========== Media Query (Medium Screens) ========== */
@media screen and (max-width: 1024px){
    .container {
        width: 86%;
        width: var(--container-width-md);
    }
    section {
        margin-top: 6rem;
    }
    #blob {
        display: none;
    }

    #blur {
        -webkit-backdrop-filter: none;
                backdrop-filter: none;
    }
}

/* ========== Media Query (Small Screens) ========== */
@media screen and (max-width: 600px){
    .container {
        width: 90%;
        width: var(--container-width-sm);
    }
    section > h2 {
        margin-bottom: 2rem;
    }
    #blob {
        display: none;
    }

    #blur {
        -webkit-backdrop-filter: none;
                backdrop-filter: none;
    }
}
